/* color Variables */
/* // color Variables */
/*Media Query Mixin For Help*/
/* Prefix Mixin */
@import url("https://fonts.googleapis.com/css?family=El+Messiri&display=swap");
body {
  font-family: "El Messiri", sans-serif, arial;
  background-color: #fff;
  font-size: 14px;
}

ul,
ol {
  list-style: none;
  padding-left: 0;
  padding-right: 0;
}

/* background--color */
.bg-gray1 {
  background-color: #aaa;
}

.bg-gray2 {
  background-color: #585858;
}

.bg-gray3 {
  background-color: #eee;
}

.bg-white {
  background-color: #ffffff;
}

.bg-orange1 {
  background-color: #fca31b;
}

.bg-orange2 {
  background-color: #fc751b !important;
}

/* background--color */
/* text-color */
.text-gray1 {
  color: #aaa;
}

.text-gray2 {
  color: #585858;
}

.text-gray3 {
  color: #eee;
}

.text-white {
  color: #ffffff;
}

.text-orange1 {
  color: #fca31b;
}

.text-orange2 {
  color: #fc751b;
}

/* // text-color */
/*  scrollbar*/
/* width */
::-webkit-scrollbar {
  width: 18px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #fca31b;
  border-left: 1px solid #eee;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #fc751b;
}

/*  // scrollbar*/
@media screen and (max-width: 991px) {
  body {
    font-size: 12px;
    padding-top: 80px;
  }
  .h3 {
    font-size: 1.2rem;
  }
}

.section__title--main {
  margin-top: 15px;
  margin-bottom: 15px;
}

.section__title--main .section-title__circle {
  border: 2px solid #fca31b;
  border-radius: 50%;
  display: inline-block;
  text-align: center;
  padding: 6px;
  width: 50px;
  height: 50px;
}

.section__title--main .section-title__circle :first-child {
  text-align: center;
  font-size: 23px;
  color: #585858;
}

.section__title--main .section-title__text {
  color: #585858;
  padding-inline-start: 10px;
}

.head-flag span {
  background-color: #fca31b;
  border-radius: 0 15px 0 0;
  padding: 4px 15px 0 20px;
  margin-left: 20px;
  font-size: 20px;
  color: #ffffff;
  position: relative;
}

.head-flag span svg {
  position: absolute;
  top: 0;
  height: 100%;
  left: -10px;
}

/* Card Shop */
.card--shop {
  margin-bottom: 3rem;
}

.card--shop .card-head {
  position: relative;
}

.card--shop .card-head .shop-logo {
  border: 2px solid #fca31b;
  border-radius: 50%;
  width: 35%;
  position: absolute;
  bottom: -10px;
  right: 15px;
}

.card--shop .card-head .shop-logo img {
  width: 100%;
  border-radius: 50%;
}

.card--shop .card-head .rate {
  position: absolute;
  left: 10px;
  bottom: 14px;
}

.card--shop .card-body {
  background-color: #eee;
  border-top: 1.5px solid #fca31b;
}

.card--shop .card-body .card-body__title i {
  color: #fca31b;
  font-size: 12px;
}

.card--shop .card-body .card-body__title span {
  color: #585858;
  font-size: 1rem;
  font-weight: bold;
}

.card--shop .card-body .card-body__addess,
.card--shop .card-body .card-body__phone {
  text-decoration: none;
}

.card--shop .card-body .card-body__addess p i,
.card--shop .card-body .card-body__phone p i {
  color: #fca31b;
  font-size: 12px;
}

.card--shop .card-body .card-body__addess p span,
.card--shop .card-body .card-body__phone p span {
