  font-size: 0.7rem;
  font-weight: bold;
}

.card--shop .card-body .social {
  display: flex;
  margin: 3px 0 0 0;
}

.card--shop .card-body .social li:not(:last-of-type) {
  margin-inline-end: 10px;
}

.card--shop .card-body .social li i {
  font-size: 25px;
  color: #585858;
}

.card--shop .card-body .social li i:hover {
  color: #fca31b;
  transition: color 0.3s ease-in-out;
}

/* End Card Shop */
/* Card Shop */
.card--offer {
