}

.card--offer .card-body {
  background-color: #eee;
  border-top: 1.5px solid #fca31b;
}

.card--offer .card-body .card-body__title span {
  color: #585858;
  font-size: 1rem;
  font-weight: bold;
}

.card--offer .card-body .card-body__title .under-line {
