  font-weight: bold;
}

.card--events .card-body .offer-container {
  text-align: left;
}

.card--events .card-body .offer-container .take-offer {
  text-decoration: none;
  color: #585858;
  background-color: #fca31b;
  padding: 2px 7px;
  font-size: 0.8rem;
