/* End Card Events */
/* header */
.yalla-header .navbar .yalla-header__top {
  justify-content: space-between;
  position: relative;
}

.yalla-header .navbar .yalla-header__top .yalla-header__logo-box a img {
  width: 160px;
}

.yalla-header .navbar .yalla-header__top .social {
  display: flex;
  flex-direction: row-reverse;
  margin: 0;
  padding: 10px 10px 10px 10px;
}

.yalla-header .navbar .yalla-header__top .social li a {
  margin-right: 13px;
  color: #ffffff;
}

.yalla-header .navbar .yalla-header__top .social li a:hover {
  color: #fca31b;
  transition: color ease-in-out 0.3s;
}

.yalla-header .navbar .yalla-header__top .social li a i {
  font-size: 30px;
}

.yalla-header .navbar .yalla-header__top .navbar-toggler {
  background-color: #fca31b;
  position: absolute;
  right: -5px;
  top: 12px;
}

.yalla-header .navbar .yalla-header__top .navbar-toggler .navbar-toggler-icon {
  height: 0.2rem;
  display: block;
  background-color: #fff;
  margin: 5px 0;
}

.yalla-header .navbar .main-navbar li a,
.yalla-header .navbar .sign-nav li a {
  color: #ffffff;
  font-size: 1.5rem;
  position: relative;
}

.yalla-header .navbar .main-navbar li a:hover,
.yalla-header .navbar .sign-nav li a:hover {
  color: #fca31b;
  transition: color 0.4s;
}

.yalla-header .navbar .main-navbar li a::after,
.yalla-header .navbar .sign-nav li a::after {
  content: "";
  display: block;
  width: 0;
  height: 2px;
  background: #fca31b;
  transition: width 0.3s;
}

.yalla-header .navbar .main-navbar li a:hover::after,
.yalla-header .navbar .sign-nav li a:hover::after {
  width: 95%;
}

.yalla-header .navbar .main-navbar li.active a,
.yalla-header .navbar .sign-nav li.active a {
  color: #fca31b;
}

.yalla-header .navbar .main-navbar li.active a::after,
.yalla-header .navbar .sign-nav li.active a::after {
  content: "";
  display: block;
  width: 95%;
  height: 2px;
  background: #fca31b;
  transition: width 0.3s;
}

/* // header */
/* responsive */
@media screen and (max-width: 991px) {
  .yalla-header .navbar {
    position: fixed;
    left: 0;
    top: 0;
    right: 0;
    z-index: 999;
  }
  .yalla-header .navbar .yalla-header__top .yalla-header__logo-box {
    width: 100%;
    display: flex;
    justify-content: center;
  }
  .yalla-header .navbar .yalla-header__top .yalla-header__logo-box .navbar-brand {
    margin: 0;
  }
  .yalla-header .navbar .yalla-header__top .social {
    display: none;
    justify-content: center;
  }
  .yalla-header .navbar .yalla-header__top .social li a i {
    font-size: 30px;
  }
}

