/* Start Index */
.main-search {
  background-color: #fca31b;
  padding: 10rem 0;
}

.main-search .head-section {
  font-size: 3.7rem;
  font-weight: bold;
}

.main-search .search-container {
  margin-top: 10px;
  border-radius: 20px;
  overflow: hidden;
  position: relative;
}

.main-search .search-container .search-input {
  width: 100%;
  height: 46px;
  padding: 8px 12px;
  border-radius: 23px;
  font-size: 20px;
  font-weight: bolder;
  border: 1px solid #868686;
}

.main-search .search-container .search-button {
  position: absolute;
  background: transparent;
  border: none;
  left: 8px;
  top: 50%;
  cursor: pointer;
  transform: translateY(-43%);
}

.main-search .search-container .search-button i {
  font-size: 30px;
  color: #fca31b;
  text-shadow: 1px 2px 1px #585858;
}

.main-search .category-links {
  display: flex;
  justify-content: center;
  margin-top: 34px;
}

.main-search .category-links li {
  margin-left: 10px;
  margin-right: 10px;
}

.main-search .category-links li a {
  text-decoration: none;
}

.main-search .category-links li a:hover i {
  color: #ffffff;
  background-color: #585858;
  transition: all 0.3s ease-in-out;
  border: 1.5px solid #ffffff;
}

.main-search .category-links li a:hover p {
  text-shadow: 1px 1px 1px #585858;
  transition: text-shadow 0.3s ease-in-out;
}

.main-search .category-links li a i {
  background-color: #ffffff;
  color: #585858;
  border: 1.5px solid #585858;
  font-size: 50px;
  width: 90px;
  height: 90px;
  padding-top: 17px;
  border-radius: 50%;
}

.main-search .category-links li a p {
  margin-top: 10px;
  color: #ffffff;
}

.main-search .category-box .category-box__btn-more {
  display: none;
}

/* End Index */
/* Media */
@media screen and (max-width: 991px) {
  .main-search {
    padding: 3rem 0 0 0;
  }
  .main-search .head-section {
    font-size: 2rem;
  }
  .main-search .search-container {
    margin-top: 10px;
    position: relative;
  }
  .main-search .search-container .search-input {
    width: 100%;
    height: 47px;
    padding: 8px 12px;
    border-radius: 20px;
    font-size: 20px;
  }
  .main-search .search-container .search-button {
    left: 8px;
    top: 50%;
  }
  .main-search .search-container .search-button i {
    font-size: 30px;
  }
  .main-search .category-box {
    position: relative;
  }
  .main-search .category-box .category-links {
    flex-wrap: wrap;
  }
  .main-search .category-box .category-links li {
    margin-left: 10px;
    margin-right: 10px;
  }
  .main-search .category-box .category-links li:nth-of-type(n + 3) {
    display: none;
  }
  .main-search .category-box .category-links li a {
    text-decoration: none;
  }
  .main-search .category-box .category-links li a i {
    font-size: 24px;
    width: 50px;
    height: 50px;
    padding-top: 13px;
    border-radius: 50%;
  }
  .main-search .category-box .category-links li a p {
    margin-top: 10px;
    font-size: 12px;
  }
  .main-search .category-box .category-box__btn-more {
    display: block;
    position: absolute;
    bottom: 20%;
    left: 10%;
    cursor: pointer;
    font-size: 14px;
    text-align: center;
    width: 40px;
    height: 20px;
    border-radius: 20px;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
    background-color: #fff;
    color: #555;
  }
}

