    display: inline-block;
    width: auto;
    padding: 5px 15px;
    border-radius: 5px;
    margin-left: 5px;
    max-width: 80%;
  }
}

/* // Media */
/* member-profile */
.member-profile .member-head .member__profile-img {
  position: relative;
  min-height: 100px;
}

.member-profile .member-head .member__profile-img h2 {
  font-size: 1.4rem;
}

.member-profile .member-head .member__profile-img img {
  position: absolute;
  bottom: -45px;
  z-index: 3;
  left: 50%;
  transform: translateX(-50%);
  max-width: 140px;
  height: auto;
  border: 1px solid #585858;
  padding: 2.5px;
  transition: background-color 0.3s ease-in-out;
}

.member-profile .member-head .member__profile-img img:hover {
  background-color: #fca31b;
}

.member-profile .member-carousel .swiper-container--custom .swiper-wrapper .swiper-slide--custom {
  position: relative;
}

.member-profile .member-carousel .swiper-container--custom .swiper-wrapper .swiper-slide--custom .slide__popup {
  position: absolute;
  bottom: 10px;
  right: 10px;
  border-radius: 50%;
  border: 2px solid #ffffff;
  background-color: #fca31b;
  width: 40px;
  height: 40px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  transition: all 0.4s ease-in-out;
}

.member-profile .member-carousel .swiper-container--custom .swiper-wrapper .swiper-slide--custom .slide__popup:hover {
  background-color: #ffffff;
  border-color: #fca31b;
  transform: rotate(-220deg);
}

.member-profile .member-carousel .swiper-container--custom .swiper-wrapper .swiper-slide--custom .slide__popup:hover i {
  color: #fca31b;
}

.member-profile .member-carousel .swiper-container--custom .swiper-wrapper .swiper-slide--custom .slide__popup i {
  font-size: 22px;
  color: #ffffff;
}

.member-profile .member-carousel .swiper-container--custom .swiper-wrapper .swiper-slide--custom img {
  height: 500px;
}

.member-profile .member-carousel .swiper-container--custom .swiper-button-next--custom:after,
.member-profile .member-carousel .swiper-container--custom .swiper-button-prev--custom:after {
  color: #fca31b;
}

.member-profile .member-details .about .about__body {
  border: 1px solid #585858;
  border-radius: 15px 0 15px 15px;
  overflow: hidden;
}

.member-profile .member-details .about .about__body p {
  padding: 15px;
  background-color: #eee;
  color: #585858;
  margin-bottom: 0;
  font-size: 16px;
}

.member-profile .member-details .attached {
  border: 1px solid #585858;
  border-radius: 15px;
  overflow: hidden;
}

.member-profile .member-details .attached .accordion .card {
  border: none;
}

.member-profile .member-details .attached .accordion .card:not(:last-of-type) {
  border-bottom: 1px solid #585858;
}

.member-profile .member-details .attached .accordion .card:first-of-type {
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
}

.member-profile .member-details .attached .accordion .card:last-of-type {
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
}

.member-profile .member-details .attached .accordion .card .card-header {
  background-color: #ffffff;
  border-bottom: none;
}

.member-profile .member-details .attached .accordion .card .card-header .btn-link--custom {
  text-decoration: none;
  color: #fca31b;
  padding-bottom: 15px;
}

.member-profile .member-details .attached .accordion .card .card-header .btn-link--custom svg {
  max-width: 55px;
  height: auto;
}

.member-profile .member-details .attached .accordion .card .card-header .btn-link--custom .expanded {
  transition: transform 0.3s ease-in-out;
  width: 22px;
  height: 18px;
  border-radius: 50%;
}

.member-profile .member-details .attached .accordion .card .card-header .btn-link--custom[aria-expanded="true"] .expanded {
  transform: rotate(-45deg);
}

.member-profile .member-details .follow .follow__head span span svg {
  width: 20px;
}

.member-profile .member-details .follow .follow__body {
  border: 1px solid #585858;
  border-radius: 15px 0 15px 15px;
  padding: 10px;
}

.member-profile .member-details .follow .follow__body .follow__ref {
  text-align: center;
  padding: 15px;
  border: 1px solid #585858;
  border-radius: 15px;
}

.member-profile .member-details .follow .follow__body .follow__ref svg {
  width: 80%;
  height: auto;
}

.member-profile .member-details .contact .contact__head span span svg {
  width: 30px;
  left: auto;
  right: auto;
  position: relative;
}

.member-profile .member-details .contact .contact__body {
  border: 1px solid #585858;
  border-radius: 15px 0 15px 15px;
  padding: 10px;
}

.member-profile .member-details .contact .contact__body i {
  color: #585858;
}

.member-profile .member-details .contact .contact__body h6 {
  font-weight: bold;
  border-bottom: 1px solid #585858;
  padding-top: 15px;
  padding-bottom: 15px;
}

.member-profile .member-details .contact .contact__body h6 span {
  padding-inline-start: 10px;
}

.member-profile .member-details .contact .contact__body ul li {
  padding-top: 5px;
  padding-bottom: 5px;
}

.member-profile .member-details .contact .contact__body ul li span {
  padding-inline-start: 10px;
}

.member-profile .client-rating .client__posts .client__post {
  border: 1px solid #585858;
  border-radius: 15px 0 15px 15px;
  padding: 15px 35px;
  margin-bottom: 30px;
}

.member-profile .client-rating .client__posts .client__post .client__data {
  border-bottom: 1px solid #585858;
  padding-bottom: 10px;
}

.member-profile .client-rating .client__posts .client__post .client__data img {
  border: 1px solid #585858;
  max-width: 80px;
  padding: 2px;
  transition: background-color 0.3s ease-in-out;
}

.member-profile .client-rating .client__posts .client__post .client__data img:hover {
  background-color: #fca31b;
}

.member-profile .client-rating .client__posts .client__post .client__data .data__box {
  padding-right: 15px;
}

.member-profile .client-rating .client__posts .client__post .client__data .rate-number {
  background-color: #fca31b;
  border-radius: 50%;
  text-align: center;
  width: 30px;
  height: 30px;
  display: inline-block;
  color: #fff;
  text-align: center;
  line-height: 2.5;
  position: relative;
}

.member-profile .client-rating .client__posts .client__post .client__data .rate-number::before {
  content: "";
  width: 0;
  height: 0;
  border-top: 6px solid transparent;
  border-left: 12px solid #fca31b;
  border-bottom: 6px solid transparent;
  position: absolute;
  right: -9px;
  top: 28%;
}

.member-profile .client-rating .client__posts .client__post .client__data .client-name {
  color: #fca31b;
  font-size: 16px;
  margin-right: 15px;
  margin-left: 15px;
}

.member-profile .client-rating .client__posts .client__post .client__data .post__share {
  border-radius: 50%;
  transition: all 0.3s ease-in-out;
}

.member-profile .client-rating .client__posts .client__post .client__data .post__share svg path {
  transition: all 0.3s ease-in-out;
}

.member-profile .client-rating .client__posts .client__post .client__data .post__share:hover {
  background-color: #fca31b;
}

.member-profile .client-rating .client__posts .client__post .client__data .post__share:hover svg path {
  fill: #ffffff;
}

.member-profile .client-rating .client__posts .client__post .client__rate {
  border-radius: 15px;
  padding: 13px 0;
}

.member-profile .client-rating .client__posts .client__post .client__rate .rate svg {
  width: 14px;
}

.member-profile .client-rating .rate-container {
  margin-top: 30px;
  border-radius: 15px;
  border: 1px solid #585858;
  overflow: hidden;
}

.member-profile .client-rating .rate-container .rate-container__top {
  padding: 15%;
  background-color: #aaa;
  position: relative;
  min-height: 220px;
}

.member-profile .client-rating .rate-container .rate-container__top .rate__score {
  position: absolute;
  display: block;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.member-profile .client-rating .rate-container .rate-container__top .rate__score .number {
  font-size: 30px;
  font-weight: bold;
  color: #ffffff;
}

.member-profile .client-rating .rate-container .rate-container__top .rate__score .stars svg {
  width: 12px;
}

.member-profile .client-rating .rate-container .rate-container__top .rate__score .stars svg path {
  fill: #ffffff;
}

.member-profile .client-rating .rate-container .rate-container__bottom {
  padding: 30px 0;
  text-align: center;
  background-color: #585858;
}

.member-profile .client-rating .rate-container .rate-container__bottom .btn--custom {
  background-color: #ffffff;
  color: #585858;
  border-radius: 25px;
  margin-inline-end: 5px;
  margin-inline-start: 5px;
  transition: all 0.3s ease-in-out;
}

.member-profile .client-rating .rate-container .rate-container__bottom .btn--custom svg path {
  transition: all 0.3s ease-in-out;
}

.member-profile .client-rating .rate-container .rate-container__bottom .btn--custom:hover {
  background-color: #fca31b;
  color: #ffffff;
}

.member-profile .client-rating .rate-container .rate-container__bottom .btn--custom:hover svg path {
  fill: #ffffff;
}

/* // member-profile */
/* member popup */
.mfp-wrap .mfp-container .mfp-content .mfp-img {
  width: 80vw;
}

/*// member popup */
/* Media */
@media screen and (max-width: 767px) {
  .member-profile .member-head h2 {
    font-size: 1.6rem;
  }
  .member-profile .client-rating .client__posts .client__post .client__data {
    flex-direction: column;
  }
  .member-profile .client-rating .client__posts .client__post .client__data .data__box {
    padding-right: 0 !important;
  }
  .member-profile .client-rating .client__posts .client__post .client__data .rate-number {
    margin-bottom: 10px;
  }
  .member-profile .client-rating .client__posts .client__post .client__data .rate-number::before {
    left: -9px;
    right: auto;
    transform: rotateY(180deg);
  }
  .member-profile .client-rating .client__posts .client__post .client__data .post-date {
    margin-bottom: 10px;
    display: block;
  }
  .member-profile .client-rating .client__posts .client__post .client__comment {
    font-size: 14px;
  }
  .member-profile .client-rating .client-rating__row {
    flex-direction: column-reverse;
  }
  .member-profile .client-rating .client-rating__row .rate-container {
    margin-top: 0;
    margin-bottom: 30px;
  }
}

