/* Member */
.members {
  .members__wave {
    height: 150px;
    background-image: url("../imges/resource/waveBorder.png");
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;

    .member__title {
      span {
        display: inline-block;
        color: $white;
        display: inline-block;
        color: #ffffff;
        background-color: rgba(255, 255, 255, 0.596);
        padding: 6px 15px 9px 30px;
        border-radius: 30px 0px 0 30px;
      }
    }

    .members__results {
      position: absolute;
      bottom: 0px;
      right: 3%;
      padding: 10px;
      border-radius: 50%;
      color: $white;
      background-color: $gray1;
    }
  }

  .members__content {
    margin-top: 120px;
    .members__content__Categories {
      .head {
        span {
          background-color: $orange1;
          border-radius: 15px 15px 0 0;
          padding: 4px 15px 0 48px;
          color: $white;
          font-size: 20px;
          position: relative;
          i {
            display: none;
            position: absolute;
            left: 10px;
            top: 24%;
            transition: transform 0.3s ease-in-out;

            &.expanded {
              transform: rotate(-45deg);
            }
          }
        }
      }

      .body {
        background-color: $gray3;
        padding: 20px 10px 10px 10px;
        text-align: center;
        .btn--categories {
          border-radius: 12px;
          margin-bottom: 7px;
          svg {
            width: 16px;
            height: auto;
          }
          &:hover {
            svg {
              width: 16px;
              height: auto;
              path {
                fill: $white;
                transition: fill 0.3s ease-in-out;
              }
            }
          }
        }
      }
    }
    .members__content__container {
      .member {
        margin-bottom: 20px;
        .head {
          span {
            background-color: #fca31b;
            border-radius: 0 15px 0 0;
            padding: 4px 15px 0 20px;
            margin-left: 20px;
            color: #ffffff;
            font-size: 20px;
            position: relative;
            svg {
              position: absolute;
              top: 0;
              height: 100%;
              left: -10px;
            }
          }
        }

        .body {
          padding: 10px 0;
          background-color: $gray3;
          border-radius: 60px 0 0 60px;
          display: flex;

          .member__title {
            margin-bottom: 5px;
            a {
              color: $gray2;
              font-weight: bold;
              i {
                color: $orange1;
              }
            }
          }
          .rate {
            margin-bottom: 5px;
            svg {
              width: 20px;
            }
          }
          p {
            color: $gray2;
            font-size: 13px;
            i {
              color: $orange1;
            }
          }
          .social {
            li {
              height: 25px;
              a {
                height: 25px;
              }
              margin-inline-end: 10px;
              font-size: 25px;

              &:hover i {
                color: $orange1;
                transition: color 0.3s ease-in-out;
              }
              i {
                color: $gray2;
              }
            }
          }
          .member-logo {
            position: relative;
            img {
              background-color: $white;
              padding: 2px;
              position: relative;
              z-index: 3;
            }
            .member__more-detail {
              background-color: $orange1;
              color: $white;
              display: block;
              position: absolute;
              z-index: 0;
              bottom: 0px;
              left: 50%;
              width: 136px;
              padding-right: 13px;
              padding-top: 5px;
              border-radius: 0 12px 0 0;
              font-size: 13px;
              &:hover {
                text-decoration: none;
                background-color: $gray2;
                transition: background-color 0.3s ease-in-out;
              }
            }
          }
        }
      }
    } //members__content__container

    .members__content__advertisement {
      .head {
        span {
          background-color: $orange1;
          border-radius: 15px 15px 0 0;
          padding: 4px 15px 0 48px;
          color: $white;
          font-size: 20px;
        }
      }
      .body {
        background-color: $gray3;
        padding: 20px 15px 15px 15px;
        text-align: center;
        .advertise {
          background-color: $white;
          border: 1px solid $gray1;
          margin-bottom: 20px;
          img {
            display: block;
            width: 80%;
            margin: 10px auto;
          }
          .click-to-add {
            background-color: $orange1;
            padding: 5px 15px;
            border-radius: 20px;
            margin-top: 16px;
            display: inline-block;
            text-decoration: none;
            color: $gray3;
            margin-bottom: 15px;
            transition: background-color 0.3s ease-in-out;
            &:hover {
              background-color: $gray2;
            }
          }
        }
      }
    }
  } //members__content
}

/* // Member */

/* Media */
@media screen and #{$maxMobile} {
  .members {
    .members__wave {
      .member__title {
        span {
          font-size: 14px;
        }
      }
    }
    .members__content {
      margin-top: 25px;

      .members__content__Categories {
        .head span {
          font-size: 15px;
          i {
            display: block;
          }
        }
        #members-Categories-body {
          display: none;
        }
      }

      .body.sm--scroll {
        height: 200px;
        overflow-x: scroll;
        direction: ltr;
        .btn--categories {
          display: block;
          width: 100%;
          direction: rtl;
        }
      }
      .members__content__container {
        margin-top: 15px;
        .member .head {
          span {
            font-size: 14px;
          }
        }
        .member .body {
          border-radius: 15px 0 0 15px;

          .member-logo {
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            justify-content: space-between;
            height: 100%;

            img {
              border-radius: 15px !important;
              max-width: 80% !important;
              margin-bottom: 10px;
            }
            .member__more-detail {
              position: static;
              display: inline-block;
              width: auto;
              padding: 5px 15px;
              border-radius: 5px;
              margin-left: 5px;
              max-width: 80%;
            }
          }
        }
      }
    }
  }
}
/* // Media */
