/* header */
.yalla-header {
  .navbar {
    .yalla-header__top {
      justify-content: space-between;
      position: relative;

      .yalla-header__logo-box {
        a {
          img {
            width: 160px;
          }
        }
      }
      .social {
        display: flex;
        flex-direction: row-reverse;
        margin: 0;
        padding: 10px 10px 10px 10px;

        li {
          a {
            margin-right: 13px;
            color: $white;
            &:hover {
              color: $orange1;
              transition: color ease-in-out 0.3s;
            }
            i {
              font-size: 30px;
            }
          }
        }
      }
      .navbar-toggler {
        background-color: $orange1;
        position: absolute;
        right: -5px;
        top: 12px;
        .navbar-toggler-icon {
          height: 0.2rem;
          display: block;
          background-color: #fff;
          margin: 5px 0;
        }
      }
    }

    .main-navbar,
    .sign-nav {
      li a {
        color: $white;
        font-size: 1.5rem;
        position: relative;

        &:hover {
          color: $orange1;
          transition: color 0.4s;
        }
        &::after {
          content: "";
          display: block;
          width: 0;
          height: 2px;
          background: $orange1;
          transition: width 0.3s;
        }

        &:hover::after {
          width: 95%;
        }
      }
      li.active a {
        color: $orange1;
        &::after {
          content: "";
          display: block;
          width: 95%;
          height: 2px;
          background: $orange1;
          transition: width 0.3s;
        }
      }
    }
  }
}
/* // header */

/* responsive */

@media screen and #{$maxSmall} {
  .yalla-header {
    .navbar {
      position: fixed;
      left: 0;
      top: 0;
      right: 0;
      z-index: 999;
      .yalla-header__top {
        .yalla-header__logo-box {
          width: 100%;
          display: flex;
          justify-content: center;
          .navbar-brand {
            margin: 0;
          }
        }

        .social {
          display: none;
          justify-content: center;

          li a i {
            font-size: 30px;
          }
        }
      }
    }
  }
}

