footer {
  border-top: 1px solid $orange1;
  border-bottom: 1px solid $orange1;
  margin-top: 50px;
  padding: 30px 0 20px 0;
  background-color: $gray2;
  position: relative;
  .footer-list {
    margin: 0;
    li,
    li p,
    li a {
      color: $white;
      text-decoration: none;
      &:hover {
        color: $orange1;
        transition: color 0.3s ease-in-out;
      }
    }
  }
  .copy-right {
    text-align: center;
    a {
      color: $white;
      text-decoration: none;
      &:hover {
        color: $orange1;
        transition: color 0.3s ease-in-out;
      }
    }
  }
  .footer__logo {
    position: absolute;
    left: 20px;
    top: 15px;
    width: 150px;
  }
}
