@import url("https://fonts.googleapis.com/css?family=El+Messiri&display=swap");
body {
  font-family: "El Messiri", sans-serif, arial;
  background-color: #fff;
  font-size: 14px;
}
ul,
ol {
  list-style: none;
  padding-left: 0;
  padding-right: 0;
}

/* background--color */
.bg-gray1 {
  background-color: $gray1;
}
.bg-gray2 {
  background-color: $gray2;
}
.bg-gray3 {
  background-color: $gray3;
}
.bg-white {
  background-color: $white;
}
.bg-orange1 {
  background-color: $orange1;
}
.bg-orange2 {
  background-color: $orange2 !important;
}
/* background--color */

/* text-color */
.text-gray1 {
  color: $gray1;
}
.text-gray2 {
  color: $gray2;
}
.text-gray3 {
  color: $gray3;
}
.text-white {
  color: $white;
}
.text-orange1 {
  color: $orange1;
}
.text-orange2 {
  color: $orange2;
}
/* // text-color */

/*  scrollbar*/
/* width */
::-webkit-scrollbar {
  width: 18px;
}
/* Handle */
::-webkit-scrollbar-thumb {
  background: $orange1;
  border-left: 1px solid #eee;
}
/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: $orange2;
}
/*  // scrollbar*/

@media screen and #{$maxSmall} {
  body {
    font-size: 12px;
    padding-top:80px;
  }
  .h3 {
    font-size: 1.2rem;
  }
}
