/* Start Index */
.main-search {
  background-color: $orange1;
  padding: 10rem 0;
  .head-section {
    font-size: 3.7rem;
    font-weight: bold;
  }
  .search-container {
    margin-top: 10px;
    border-radius: 20px;
    overflow: hidden;
    position: relative;
    .search-input {
      width: 100%;
      height: 46px;
      padding: 8px 12px;
      border-radius: 23px;
      font-size: 20px;
      font-weight: bolder;
      border: 1px solid #868686;
    }
    .search-button {
      position: absolute;
      background: transparent;
      border: none;
      left: 8px;
      top: 50%;
      cursor: pointer;
      transform: translateY(-43%);
      i {
        font-size: 30px;
        color: $orange1;
        text-shadow: 1px 2px 1px $gray2;
      }
    }
  }
  .category-links {
    display: flex;
    justify-content: center;
    margin-top: 34px;

    li {
      margin-left: 10px;
      margin-right: 10px;
      a {
        text-decoration: none;
        &:hover i {
          color: $white;
          background-color: $gray2;
          transition: all 0.3s ease-in-out;
          border: 1.5px solid $white;
        }
        &:hover p {
          text-shadow: 1px 1px 1px $gray2;
          transition: text-shadow 0.3s ease-in-out;
        }

        i {
          background-color: $white;
          color: $gray2;
          border: 1.5px solid $gray2;
          font-size: 50px;
          width: 90px;
          height: 90px;
          padding-top: 17px;
          border-radius: 50%;
        }
        p {
          margin-top: 10px;
          color: $white;
        }
      }
    }
  }
  .category-box {
    .category-box__btn-more {
      display: none;
    }
  }
}
/* End Index */
/* Media */
@media screen and #{$maxSmall} {
  .main-search {
    padding: 3rem 0 0 0;
    .head-section {
      font-size: 2rem;
    }
    .search-container {
      margin-top: 10px;
      position: relative;
      .search-input {
        width: 100%;
        height: 47px;
        padding: 8px 12px;
        border-radius: 20px;
        font-size: 20px;
      }
      .search-button {
        left: 8px;
        top: 50%;

        i {
          font-size: 30px;
        }
      }
    }
    .category-box {
      position: relative;
      .category-links {
        flex-wrap: wrap;
        li {
          margin-left: 10px;
          margin-right: 10px;
          &:nth-of-type(n + 3) {
            display: none;
          }

          a {
            text-decoration: none;

            i {
              font-size: 24px;
              width: 50px;
              height: 50px;
              padding-top: 13px;
              border-radius: 50%;
            }
            p {
              margin-top: 10px;
              font-size: 12px;
            }
          }
        }
      }
      .category-box__btn-more {
        display: block;
        position: absolute;
        bottom: 20%;
        left: 10%;
        cursor: pointer;
        font-size: 14px;
        text-align: center;
        width: 40px;
        height: 20px;
        border-radius: 20px;
        box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
        background-color: #fff;
        color: #555;
      }
    }
  }
}
/* // Media */
